'use client'

import { useEffect, useState } from 'react'
import { useRouter } from 'next/navigation'

interface AdminGuardProps {
  children: React.ReactNode
}

export default function AdminGuard({ children }: AdminGuardProps) {
  const router = useRouter()
  const [isAllowed, setIsAllowed] = useState(false)
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    console.log('üîê AdminGuard - D√©marrage v√©rification...')
    
    // Petite pause pour laisser le temps au localStorage d'√™tre rempli
    const timer = setTimeout(() => {
      checkAccess()
    }, 100) // 100ms de d√©lai

    return () => clearTimeout(timer)
  }, [])

  const checkAccess = () => {
    console.log('üìä AdminGuard - V√©rification en cours...')
    
    // 1. V√©rifier le token
    const token = localStorage.getItem('access_token')
    console.log('   Token pr√©sent?', !!token)
    
    if (!token) {
      console.log('‚ùå Pas de token -> Redirection login')
      router.push('/login?redirect=/admin')
      return
    }

    // 2. V√©rifier les donn√©es membre
    const memberStr = localStorage.getItem('member')
    console.log('   Donn√©es membre pr√©sentes?', !!memberStr)
    
    if (!memberStr) {
      console.log('‚ùå Pas de donn√©es membre -> Redirection home')
      router.push('/')
      return
    }

    try {
      const member = JSON.parse(memberStr)
      console.log('   R√¥le d√©tect√©:', member.role)
      
      // 3. V√©rifier si admin
      const isAdmin = member.role === 'ADMIN' || member.role === 'WEBMASTER'
      console.log('   Est admin?', isAdmin)
      
      if (!isAdmin) {
        console.log('‚ùå Non admin -> Redirection home')
        router.push('/')
        return
      }

      // 4. Si tout est bon
      console.log('‚úÖ AdminGuard - Acc√®s AUTORIS√â!')
      setIsAllowed(true)
      
    } catch (error) {
      console.error('üí• Erreur parsing:', error)
      router.push('/login')
    } finally {
      setIsLoading(false)
    }
  }

  // Pendant le chargement
  if (isLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-50">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto mb-4"></div>
          <p className="text-gray-600">V√©rification des permissions admin...</p>
          <p className="text-gray-400 text-sm mt-2">
            Token: {localStorage.getItem('access_token') ? '‚úì' : '‚úó'}
          </p>
        </div>
      </div>
    )
  }

  // Si pas autoris√© (mais isLoading est false, donc on a d√©j√† redirig√©)
  if (!isAllowed) {
    return null
  }

  // Si autoris√©
  console.log('üéØ AdminGuard - Rendu du contenu admin')
  return <>{children}</>
}